.appMain {
    background: black;
    content: " ";
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    position: absolute;
    color: #0000;
    display: flex;
}

.gta1{    grid-template-areas: "a";}
.gta2{    grid-template-areas: "a a";}
.gta3{    grid-template-areas: "a a a";}

.games {
    background: black ;
    position: absolute;
    width: 100%;
    display: grid;
    grid-gap: 10px;
    grid-auto-columns: 600px;
    grid-column-gap: 20px;
    top: 30%;
}

.game {
    width: 600px;
    height: 200px;
    background-color: black;
}

.gameTittle {
    color: white;
    font-size: 100%;
    font-family: 'Yanone Kaffeesatz', sans-serif;
    margin: 5px;
}

.gameButton {
    color: white;
    background-color: black;
    border: 2px #ffffff ridge;
    border-radius: 5px;
    width: 100%;
    height: 25px;
    align-self: end;
    margin: 10px;
}

.gameImage {
    position: relative;
    max-width: 50%;
    left: 25%;
    max-height: 50%;
    border-radius: 5px;
}